import { UPDATE_NOT_ADDED_USERS } from '../../consts/dropdownReducer'

const updateNotAddedUsers = (users, addedUsers) => ({
  type: UPDATE_NOT_ADDED_USERS,
  payload: users.filter(user => (
    !addedUsers.some(addedUser => (
      user.id === addedUser.id
    ))
  ))
})

export default updateNotAddedUsers


