import glamorous from 'glamorous'
import React, { Component } from 'react'

const InputElement = glamorous.input({
  width: 'calc(100% - 10px)',
  height: '35px',
  paddingLeft: '10px',
  border: '2px solid #f0f2f4',
  color: '#000',
  fontFamily: 'TextBook, sans-serif',
  fontSize: '16px',
  fontWeight: 'normal',
  borderRadius: '3px',
  outline: 'none',
  transition: 'all .15s ease-in-out',
  '::-webkit-input-placeholder, ::-moz-placeholder, :-moz-placeholder': {
    color: '#c0c5ca',
  },
  ':hover,:focus': {
    border: '2px solid #55a8fe'
  }
});

export default class Input extends Component {
  errorStyles = {
    borderColor: '#f95345'
  }

  render() {
    const { isError, css, ...otherProps } = this.props

    return <InputElement {...otherProps} css={isError ? {...this.errorStyles, ...css} : css} />
  }
}


