import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Div } from 'glamorous'
import Scrollbar from 'react-custom-scrollbars'
import DropdownOption from './ui/DropdownOption'
import addUser from '../action-creators/eventsReducer/addUser' 
import getScrollbar from '../action-creators/dropdownReducer/getScrollbar'
import PropTypes from 'prop-types'

class AutoCompleteMenu extends Component {
  static propTypes = {
    addUser: PropTypes.func.isRequired,
    getScrollbar: PropTypes.func.isRequired
  }

  menuStyles = {
    width: '100%',
    borderRadius: '4px',
    overflow: 'hidden',
    boxShadow: '0px 0px 11px 2px #d5dfe9',
    marginLeft: '2px'
  }

  renderItem = (item, highlighted, onClick, id) => (
    <DropdownOption
      key={item.id}
      name={item.login}
      homeFloor={item.homeFloor}
      avatarSrc={item.avatarUrl}
      highlighted={highlighted}
      onClick={onClick}
      id={highlighted ? id : null}
    /> 
  )

  findItems = () => {
    const { notAddedUsers, inputValue } = this.props
    const isItemMatching = (login, value) => login.toLowerCase().includes(value.trim().toLowerCase())

    return notAddedUsers.filter(user => isItemMatching(user.login, inputValue))
  }

  renderItems = () => {
    const { highlightedIndex, addUser, activeId } = this.props
    const foundItems = this.findItems()

    return foundItems.map((user, index) => {
      const isHighlighted = highlightedIndex === index;
      const onClick = () => addUser(user)

      return this.renderItem(user, isHighlighted, onClick, activeId)
    })
  }

  scrollbarTrackVertical = ({ style, props }) => {
    const { noScrollbar } = this.props
    
    const trackStyles = !noScrollbar ? 
    {
      display: 'inline-block',
      height: '100%',
      backgroundColor: '#ffffff',
      width: '22px',
      right: '0px',
      top: '0px',
      boxSizing: 'border-box',
      paddingBottom: '10px'
    } :
    { display: 'none' }

    return <div
    style={{
      ...style,
      ...trackStyles
    }}
    {...props}
    ></div>
  }

  scrollbarThumb = ({ style, props }) => {
    const { noScrollbar } = this.props

    const thumbStyles = !noScrollbar ? 
    {
      width: '11px',
      backgroundColor: '#d5dfe9',
      borderRadius: '5px',
      marginTop: '5px',
      marginLeft: '5px'
    } :
    { display: 'none' }

    return <div
    style={{
      ...style,
      ...thumbStyles
    }}
    {...props}
    ></div>
  }

  componentDidMount () {
    const { getScrollbar } = this.props

    getScrollbar(this.scrollbars)
  }

  render () {
    return (
      <Div 
        css={this.menuStyles} 
        ariaSelected="true" 
      >
        <Scrollbar
          autoHeight
          autoHeightMax={140}
          renderThumbVertical={this.scrollbarThumb}
          renderTrackVertical={this.scrollbarTrackVertical}
          hideTracksWhenNotNeeded
          ref={s => { this.scrollbars = s }}
        >
          {
            this.renderItems()
          }
        </Scrollbar>
      </Div>
    )
  }
}

const mapStateToProps = state => ({
  activeId: state.dropdownReducer.activeId,
  notAddedUsers: state.dropdownReducer.notAddedUsers,
  highlightedIndex: state.dropdownReducer.highlightedIndex,
  inputValue: state.dropdownReducer.inputValue
})

const mapDispatchToProps = {
  addUser,
  getScrollbar
}

export default connect(mapStateToProps, mapDispatchToProps)(AutoCompleteMenu)


