import getLocalStorageItems from './getLocalStorageItems'

const addLocalStorageEvent = (newEvent, id) => {
  getLocalStorageItems('events')
  .then(events => {
    localStorage.setItem('events', JSON.stringify([ ...events, { ...newEvent, id } ]))
  })
}

export default addLocalStorageEvent


