import React, { Component, Fragment } from 'react'
import ErrorText from './ui/ErrorText'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

class HandleError extends Component {
  static propTypes = {
    message: PropTypes.string.isRequired,
    isError: PropTypes.bool.isRequired,
    isAllErrorsShown: PropTypes.bool.isRequired
  }

  render () {
    const { isError, message, isAllErrorsShown } = this.props

    return (
      <Fragment>
        {isError && isAllErrorsShown &&
          <ErrorText>
            {message}
          </ErrorText>
        }
      </Fragment>
    )
  }
}

const mapStateToProps = state => ({
  isAllErrorsShown: state.errorReducer.isAllErrorsShown
})

export default connect(mapStateToProps)(HandleError)


