import React, { Component } from 'react'
import Input from './ui/Input'
import DayPickerInput from 'react-day-picker/DayPickerInput'
import { connect } from 'react-redux'
import updateDate from '../action-creators/eventsReducer/updateDate'
import changeValue from '../action-creators/dateReducer/changeValue'
import PropTypes from 'prop-types'
import { dateShape } from '../intl/shapes'
import addLabel from './HOC/addLabel'

class UpgradedInput extends Component {
  focus = () => {
    this.input.focus();
  }

  render () {
    return <Input innerRef={ref => this.input = ref} {...this.props} />
  }
}

class DateSetterInput extends Component {
  static propTypes = {
    date: dateShape.isRequired,
    id: PropTypes.string,
    updateDate: PropTypes.func.isRequired,
    changeValue: PropTypes.func.isRequired
  }

  now = new Date()

  updateDate = day => {
    const { monthsValue, updateDate, changeValue } = this.props

    const date = day.getDate()
    const month = day.getMonth()

    updateDate(month, date)
    changeValue(monthsValue[month], date)
  }

  modifiersStyles = {
    highlightedDate: {
      color: '#fff',
      backgroundColor: '#55a8fe',
      borderRadius: '50%'
    }
  }

  render() {
    const { date, id, locale, months, daysShort, labels, firstDay, value } = this.props

    const modifiers = {
      highlightedDate: new Date(this.now.getFullYear(), date.startingMonth, date.startingDate) 
    }
    
    return (
      <DayPickerInput
        component={UpgradedInput}
        id={id}
        value={value} 
        dayPickerProps={{
          locale,
          months,
          labels,
          onDayClick: this.updateDate,
          weekdaysShort: daysShort,
          firstDayOfWeek: firstDay,
          modifiers,
          modifiersStyles: this.modifiersStyles,
          disabledDays: [{
            before: this.now
          }],
          fromMonth: this.now
        }}
        inputProps={{
          onChange: e => {
            e.target.value = value
          },
          placeholder: ''
        }}
      />
    )
  }
}

const mapStateToProps = state => ({
  date: state.eventsReducer.currentEvent.date,
  value: state.dateReducer.value,
  labels: state.dateReducer.labels,
  monthsValue: state.dateReducer.monthsValue,
  months: state.dateReducer.months,
  daysShort: state.dateReducer.daysShort,
  firstDay: state.dateReducer.firstDay
})

const mapDispatchToProps = {
  updateDate,
  changeValue
}

const ConnectedComponent = connect(mapStateToProps, mapDispatchToProps)(DateSetterInput)

export default addLabel(ConnectedComponent)


