import React from 'react'
import Headline from '../ui/Headline'
import { Div } from 'glamorous'
import PropTypes from 'prop-types'
import { enableUniqueIds } from 'react-html-id'
import getDisplayName from './getDisplayName'
import { stylesShape } from '../../intl/shapes'

const addLabel = Component => {
  class AddLabel extends React.Component {
    static propTypes = {
      css: stylesShape,
      label: PropTypes.string
    }

    constructor (props) {
      super(props)
      enableUniqueIds(this)
    }
    
    headlineStyles = {
      marginBottom: '4px'
    }

    render () {
      const { css, label, ...otherProps } = this.props

      return (
        <Div css={css}>
          <label htmlFor={this.nextUniqueId()}>
            <Headline css={this.headlineStyles}>{label}</Headline>
          </label>

          <Component {...otherProps} id={this.lastUniqueId()} />
        </Div>
      )
    }
  }

  const componentName = getDisplayName(AddLabel)

  AddLabel.displayName = `AddLabel(${componentName})`

  return AddLabel
}

export default addLabel


