import React, { Component } from 'react'
import Input from './ui/Input'
import { connect } from 'react-redux';
import setEventName from '../action-creators/eventsReducer/setEventName'
import checkForNameError from '../action-creators/errorReducer/checkForNameError'
import PropTypes from 'prop-types'
import { injectIntl, intlShape } from 'react-intl'
import HandleErrors from './HandleErrors'
import addLabel from './HOC/addLabel'

class EventNameSetter extends Component {
  static propTypes = {
    intl: intlShape.isRequired,
    setEventName: PropTypes.func.isRequired,
    eventName: PropTypes.string.isRequired,
    placeholder: PropTypes.string,
    nameError: PropTypes.bool,
  }

  componentDidUpdate () {
    const { checkForNameError, eventName } = this.props

    checkForNameError(eventName)
  }

  handleChange = e => {
    const { setEventName } = this.props
    
    setEventName(e.target.value)
  }

  render() {
    const { eventName, nameError, intl: { formatMessage }, isAllErrorsShown, ...otherProps } = this.props
    const errorText = formatMessage({ id: 'meeting.eventNameSetter.errorText' })

    return (
      <div>
        <HandleErrors
          message={errorText}
          isError={nameError}
        />

        <Input
          value={eventName}
          isError={nameError && isAllErrorsShown}
          onChange={this.handleChange}
          {...otherProps}
        />
      </div>
    )
  }
}

const mapStateToProps = state => ({
  eventName: state.eventsReducer.currentEvent.name,
  nameError: state.errorReducer.nameError,
  isAllErrorsShown: state.errorReducer.isAllErrorsShown
})

const mapDispatchToProps = {
  setEventName,
  checkForNameError
}

const ConnectedComponent = connect(mapStateToProps, mapDispatchToProps)(injectIntl(EventNameSetter))

export default addLabel(ConnectedComponent)


