import React, { Component, Fragment } from 'react'
import { Div } from 'glamorous'
import GreyBtn from './ui/GreyBtn'
import SubmitBtn from './ui/SubmitBtn'
import createEvent from '../action-creators/eventsReducer/createEvent'
import editEvent from '../action-creators/eventsReducer/editEvent'
import deleteEvent from '../action-creators/eventsReducer/deleteEvent'
import triggerModal from '../action-creators/pageReducer/triggerModal'
import { 
  TRIGGER_CREATE_MODAL,
  TRIGGER_EDIT_MODAL
} from '../consts/pageReducer'
import resetValues from '../action-creators/eventsReducer/resetValues'
import PropTypes from 'prop-types'
import { eventShape } from '../intl/shapes'
import { injectIntl, intlShape } from 'react-intl'
import { connect } from 'react-redux'
import showAllErrors from '../action-creators/errorReducer/showAllErrors'

class BottomBtns extends Component {
  static propTypes = {
    intl: intlShape.isRequired,
    onCancel: PropTypes.func.isRequired,
    edit: PropTypes.bool,
    currentEvent: eventShape.isRequired,
    createEvent: PropTypes.func.isRequired,
    editEvent: PropTypes.func.isRequired,
    deleteEvent: PropTypes.func.isRequired,
    triggerModal: PropTypes.func.isRequired,
    nameError: PropTypes.bool.isRequired,
    parti—ÅipantsError: PropTypes.bool.isRequired,
    roomError: PropTypes.bool.isRequired,
    capacityError: PropTypes.bool.isRequired,
    timeError: PropTypes.oneOfType([
      PropTypes.bool,
      PropTypes.number // 1 or 0
    ]).isRequired,
    showAllErrors: PropTypes.func.isRequired,
    onDelete: PropTypes.func.isRequired
  }
  
  containerStyles = {
    width: '100%',
    height: '80px',
    display: 'flex',
    flex: '0 0 auto',
    paddingTop: '30px',
    alignItems: 'center',
    justifyContent: 'center',
    '@media(max-width: 1180px)': {
      position: 'relative',
      justifyContent: 'flex-end',
      borderTop: 'none',
      backgroundColor: '#fff'
    }
  }
  
  submitBtnStyles = {
    marginLeft: '20px',
    '@media(max-width: 1180px)': {
      marginRight: '5%'
    }
  }

  deleteBtnStyles = {
    margin: '0px 10px',
    '@media(max-width: 1180px)': {
      display: 'none'
    }
  }

  saveBtnStyles = {
    '@media(min-width: 1180px)': {
      backgroundColor: '#e9ecef',
      color: '#000000',
      ':hover,:focus': {
        backgroundColor: '#dde0e4'
      },
      ':active': {
        backgroundColor: '#c8cdd3'
      }
    },
    '@media(max-width: 1180px)': {
      marginRight: '5%',
      marginLeft: '10px'
    }
  }

  greyDeleteBtnStyles = {
    margin: '0px 15px'
  }

  checkForErrors = () => {
    const { 
      nameError,
      parti—ÅipantsError,
      roomError,
      capacityError,
      timeError 
    } = this.props

    return nameError || parti—ÅipantsError || roomError || capacityError || timeError
  }

  handleCreate = () => {
    const { createEvent, triggerModal, showAllErrors, currentEvent } = this.props 
    const isErrors = this.checkForErrors()

    if(!isErrors) {
      createEvent(currentEvent)
      triggerModal(TRIGGER_CREATE_MODAL)
    }

    else {
      showAllErrors()
    }
  }

  handleEdit = () => {
    const { editEvent, triggerModal, currentEvent, showAllErrors, resetValues } = this.props 
    const isErrors = this.checkForErrors()

    if(!isErrors) {
      editEvent(currentEvent, currentEvent.id)
      triggerModal(TRIGGER_EDIT_MODAL)
      resetValues()
    }

    else {
      showAllErrors()
    }
  }

  render () {
    const { onCancel, onDelete, edit, intl: { formatMessage }, breakpoint } = this.props
    const cancelBtnText = formatMessage({id: 'meeting.bottomBtns.cancelBtnText'})
    const deleteBtnText = formatMessage({id: 'meeting.bottomBtns.deleteBtnText'})
    const saveBtnText = formatMessage({id: 'meeting.bottomBtns.saveBtnText'})
    const createBtnText = formatMessage({id: 'meeting.bottomBtns.createBtnText'})

    return (
      <Div css={this.containerStyles}>
        <GreyBtn
          text={cancelBtnText}
          onClick={onCancel}
        />

        {edit ?
          <Fragment>
            {window.innerWidth > breakpoint &&
              <GreyBtn
                text={deleteBtnText}
                onClick={onDelete}
                css={this.greyDeleteBtnStyles}
              />
            }
            
            <SubmitBtn
              text={saveBtnText}
              onClick={this.handleEdit}
              css={this.saveBtnStyles}
            />
          </Fragment>

          :

          <SubmitBtn
            text={createBtnText}
            onClick={this.handleCreate}
            css={this.submitBtnStyles}
          />
        }
      </Div>
    )
  }
}

const mapStateToProps = state => ({
  breakpoint: state.pageReducer.mobileBreakpoint,
  currentEvent: state.eventsReducer.currentEvent,
  nameError: state.errorReducer.nameError,
  parti—ÅipantsError: state.errorReducer.parti—ÅipantsError,
  roomError: state.errorReducer.roomError,
  capacityError: state.errorReducer.capacityError,
  timeError: state.errorReducer.timeError,
})

const mapDispatchToProps = {
  createEvent,
  editEvent,
  deleteEvent,
  triggerModal,
  resetValues,
  showAllErrors
}

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(BottomBtns))


