const decreaseTime = (timeStr, subject, lowBorder, step) => {
  let time = Number(timeStr)
  
  if(time >= lowBorder) {
    time = time - step
  }

  time = time.toString()

  if(time < 10) {
    time = `0${time}`
  }

  return { 
    type: subject,
    payload: time
  }
}

export default decreaseTime



