import React, { Component } from 'react'
import { Button } from 'glamorous'
import PropTypes from 'prop-types'
import { stylesShape } from '../../intl/shapes'

export default class DeleteBtn extends Component {
  static propTypes = {
    text: PropTypes.string.isRequired,
    onClick: PropTypes.func.isRequired,
    css: stylesShape
  }

  mobileDeleteBtnStyles = {
    width: '100%',
    backgroundColor: '#fff',
    color: '#ff3333',
    textAlign: 'center',
    height: '60px',
    fontFamily: 'TextBook, sans-serif',
    fontWeight: 'normal',
    fontSize: '17px',
    cursor: 'pointer',
    border: 'none',
    transition: '.15s ease-in-out',
    outline: 'none',
    ':focus,:active': {
      color: '#f00'
    }
  }

  render () {
    const { text, onClick, css } = this.props

    const allDeleteBtnStyles = { ...this.mobileDeleteBtnStyles, ...css, }

    return (
      <Button css={allDeleteBtnStyles} onClick={onClick}>
        {text}
      </Button>
    )
  }
}


