import React, { Component } from 'react'
import addLabel from './HOC/addLabel'
import { Div, Input, Span } from 'glamorous'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

class TimeInput extends Component {
  state = {
    focused: false
  }

  static propTypes = {
    onHoursChange: PropTypes.func.isRequired,
    onMinutesChange: PropTypes.func.isRequired,
    hours: PropTypes.string.isRequired,
    minutes: PropTypes.string.isRequired,
    increaseHours: PropTypes.func.isRequired,
    increaseMinutes: PropTypes.func.isRequired,
    decreaseHours: PropTypes.func.isRequired,
    decreaseMinutes: PropTypes.func.isRequired,
    limitError: PropTypes.bool.isRequired,
    timeError: PropTypes.oneOfType([
      PropTypes.bool,
      PropTypes.number
    ]).isRequired,
    isAllErrorsShown: PropTypes.bool.isRequired
  }

  invisibleInputStyles = {
    border: 'none',
    backgroundColor: 'transparent',
    fontFamily: 'TextBook, sans-serif',
    fontSize: '16px',
    fontWeight: 'normal',
    borderRadius: '3px',
    transition: 'all .15s ease-in-out',
    textAlign: 'right',
    outline: 'none',
    width: '18px',
    height: '100%',
    overflow: 'hidden'
  }

  containerStyles = {
    display: 'flex',
    alignItems: 'center',
    width: '100%',
    height: '37px',
    paddingLeft: '10px',
    borderRadius: '3px',
    border: '2px solid #f0f2f4',
    boxSizing: 'content-box',
    color: '#000',
    transition: 'all .15s ease-in-out',
    '::-webkit-input-placeholder, ::-moz-placeholder, :-moz-placeholder': {
      color: '#c0c5ca'
    },
    ':hover': {
      border: '2px solid #55a8fe'
    }
  }

  containerFocusedStyles = {
    border: '2px solid #55a8fe'
  }

  errorStyles = {
    borderColor: '#f95345'
  }

  colonStyles = {
    position: 'relative',
    left: '1px'
  }

  triggerFocus = () => {
    this.setState(prevState => ({
      focused: !prevState.focused
    }))
  }

  handleHoursChange = e => {
    const { increaseHours, decreaseHours } = this.props

    if(e.key === 'ArrowUp') {
      increaseHours()
    }
    
    else if(e.key === 'ArrowDown') {
      decreaseHours()
    }
  }

  handleMinutesChange = e => {
    const { increaseMinutes, decreaseMinutes } = this.props
    
    if(e.key === 'ArrowUp') {
      increaseMinutes()
    }
    
    else if(e.key === 'ArrowDown') {
      decreaseMinutes()
    }
  }

  render () {
    const 
    { 
      onHoursChange,
      onMinutesChange,
      hours,
      minutes,
      timeError,
      limitError,
      isAllErrorsShown
    } = this.props

    let containerStyles = this.containerStyles

    if(this.state.focused) {
      containerStyles = { ...this.containerStyles, ...this.containerFocusedStyles }
    }

    if((timeError || limitError) && isAllErrorsShown) {
      containerStyles = { ...this.containerStyles, ...this.errorStyles }
    }

    return (
      <Div css={containerStyles}>
        <Input 
          type="text" 
          value={hours} 
          onChange={onHoursChange} 
          css={this.invisibleInputStyles}
          innerRef={r => (this.startTimeInput = r)}
          onFocus={this.triggerFocus}
          onBlur={this.triggerFocus}
          onKeyDown={this.handleHoursChange}
        />

        <Span css={this.colonStyles}>:</Span>

        <Input 
          type="text" 
          value={minutes} 
          onChange={onMinutesChange} 
          css={this.invisibleInputStyles}
          innerRef={r => (this.endTimeInput = r)}
          onFocus={this.triggerFocus}
          onBlur={this.triggerFocus}
          onKeyDown={this.handleMinutesChange}
        />
      </Div>
    )
  }
}

const mapStateToProps = state => ({
  timeError: state.errorReducer.timeError,
  limitError: state.errorReducer.limitError,
  isAllErrorsShown: state.errorReducer.isAllErrorsShown
})

const ConnectedComponent = connect(mapStateToProps)(TimeInput)

export default addLabel(ConnectedComponent)


