import { Button, Span, Div } from 'glamorous'
import React, { Component } from 'react'
import Paragraph from './Paragraph'
import Headline from './Headline'
import FontAwesomeIcon  from 'react-fontawesome'
import PropTypes from 'prop-types'
import { injectIntl, intlShape } from 'react-intl'
import { timeShape } from '../../intl/shapes'

class RoomCard extends Component {
  static propTypes = {
    intl: intlShape.isRequired,
    freeTime: timeShape.isRequired,
    roomName: PropTypes.string.isRequired,
    floorNumber: PropTypes.number.isRequired,
    selected: PropTypes.bool.isRequired,
    onCardSelect: PropTypes.func,
    onCardDeselect: PropTypes.func
  }

  containerStyles = {
    width: '100%',
    height: '40px',
    backgroundColor: '#e9ecef',
    borderRadius: '3px',
    position: 'relative',
    marginTop: '10px',
    cursor: 'pointer',
    border: 'none',
    outline: 'none',
    transition: '.15s ease-in-out',
    ':hover,:focus': {
      backgroundColor: '#dde0e4'
    },
    ':disabled': {
      backgroundColor: '#e1e5e8',
      color: '#c8cbcf'
    },
    ':active': {
      backgroundColor: '#c8cdd3'
    }
  }

  containerSelectedStyles = {
    backgroundColor: '#007dff',
    ':hover,:focus': {
      backgroundColor: '#0059ff'
    },
    ':active': {
      backgroundColor: '#0011fd'
    }
  }

  innerStyles = {
    height: '100%',
    display: 'flex',
    alignItems: 'center',
    marginLeft: '-5px'
  }

  headlineStyles = {
    paddingLeft: '10px'
  }

  headlineSelectedStyles = {
    color: '#ffffff'
  }

  paragraphStyles = {
    marginLeft: '20px',
    textAlign: 'left',
    whiteSpace: 'nowrap',
    overflow: 'hidden',
    textOverflow: 'ellipsis'
  }

  paragraphSelectedStyles = {
    color: '#ffffff'
  }

  iconStyles = {
    position: 'absolute',
    right: '10px',
    backgroundColor: 'transparent',
    color: '#fff',
    fontSize: '16px',
    marginTop: '-8px'
  }

  render () {
    const { 
      freeTime,
      roomName,
      floorNumber,
      selected,
      onCardSelect,
      onCardDeselect,
      intl: { 
        formatMessage
      }
    } = this.props
    const roomInfo = formatMessage({id: 'roomCard.roomInfo'}, {roomName, floorNumber})
    let containerStyles = this.containerStyles
    let headlineStyles = this.headlineStyles
    let paragraphStyles = this.paragraphStyles

    if(selected) {
      containerStyles = { ...containerStyles, ...this.containerSelectedStyles }
      headlineStyles = { ...headlineStyles, ...this.headlineSelectedStyles }
      paragraphStyles = { ...paragraphStyles, ...this.paragraphSelectedStyles }
    }
    
    return (
      <Button css={containerStyles} onClick={selected ? onCardDeselect : onCardSelect}>
        <Span css={this.innerStyles}>
          <Headline css={headlineStyles}>
            {`${freeTime.startingHours}:${freeTime.startingMinutes}`}
            ‚Äì 
            {`${freeTime.endingHours}:${freeTime.endingMinutes}`}
          </Headline>
          <Paragraph css={paragraphStyles}>{roomInfo}</Paragraph>
          {selected &&
            <Div>
              <FontAwesomeIcon  
                name="times"
                style={this.iconStyles}
              />
            </Div>
          }
        </Span>
      </Button>
    )
  }
} 

export default injectIntl(RoomCard)


