import React, { Component } from 'react'
import DateSetterInput from './DateSetterInput'
import TimeSetter from './TimeSetter'
import PropTypes from 'prop-types'
import { Div } from 'glamorous'
import { injectIntl, intlShape } from 'react-intl'

class DateAndTime extends Component {
  static propTypes = {
    css: PropTypes.object,
    intl: intlShape
  }

  containerStyles = {
    display: 'flex'
  }

  render () {
    const { css, intl: { formatMessage } } = this.props

    const containerStyles = { ...css, ...this.containerStyles }
    const dateLabel = formatMessage({id: 'meeting.timeSetter.dateLabel'})

    return (
      <Div css={containerStyles}>
        <DateSetterInput
          label={dateLabel}
        />
        <TimeSetter />
      </Div>
    )
  }
}

export default injectIntl(DateAndTime)


