import React, { Component } from 'react'
import Dropdown from './Dropdown'
import LargeHeadline from './ui/LargeHeadline'
import DateAndTime from './DateAndTime'
import { Section, Div } from 'glamorous'
import RoomCards from './RoomCards'
import BottomBtns from './BottomBtns'
import DeleteBtn from './ui/DeleteBtn'
import { connect } from 'react-redux'
import resetValues from '../action-creators/eventsReducer/resetValues'
import triggerModal from '../action-creators/pageReducer/triggerModal'
import {
  TRIGGER_CREATE_MODAL,
  TRIGGER_EDIT_MODAL,
  TRIGGER_DELETE_MODAL
} from '../consts/pageReducer'
import EventNameSetter from './EventNameSetter'
import CloseBtn from './ui/CloseBtn'
import FocusTrap from 'react-focus-trap'
import PropTypes from 'prop-types'
import { injectIntl, intlShape } from 'react-intl'

class Meeting extends Component {
  static propTypes = {
    intl: intlShape.isRequired,
    triggerModal: PropTypes.func.isRequired,
    edit: PropTypes.bool.isRequired,
    title: PropTypes.string.isRequired,
    resetValues: PropTypes.func.isRequired,
    breakpoint: PropTypes.number.isRequired
  }
  
  containerStyles = {
    position: 'absolute',
    width: '100%',
    height: '100%',
    top: '0px',
    display: 'flex',
    alignItems: 'center',
    flexDirection: 'column',
    backgroundColor: '#fff',
    zIndex: '100',
    '@media(max-width: 1180px)': {
      display: 'block'
    }
  }
  
  contentStyles = {
    width: '100%',
    display: 'flex',
    alignItems: 'center',
    flexDirection: 'column',
    flex: '1 0 auto'
  }

  childrenStyles = {
    width: 'calc(66% + 40px)',
    maxWidth: '1100px',
    position: 'relative',
    display: 'flex',
    justifyContent: 'space-between',
    '@media(max-width: 1180px)': {
      width: '100%',
      backgroundColor: '#fff',
      flexDirection: 'column',
      alignItems: 'center',
      justifyContent: 'unset',
      borderBottom: '10px solid #e9ecef',
      paddingBottom: '20px',
      ':first-child': {
        borderBottom: 'none'
      }
    }
  }

  innerSectionStyles = {
    width: 'calc(50% - 20px)',
    '@media(max-width: 1180px)': {
      width: '90%',
      margin: '0px'
    }
  } 

  headlineStyles = {
    marginBottom: '20px',
    outline: 'none',
    '@media(max-width: 1180px)': {
      ':first-child': {
        alignSelf: 'flex-start',
        marginLeft: '5%'
      }
    }
  }

  closeBtnStyles = {
    position: 'absolute',
    right: '0px',
    top: '-55px',
    '@media(max-width: 1180px)': {
      display: 'none'
    }
  }

  deleteBtnStyles = {
    borderBottom: '10px solid #e9ecef',
  }

  closeModal = () => {
    const { edit, triggerModal, resetValues } = this.props
    
    if(edit) {
      triggerModal(TRIGGER_EDIT_MODAL)
      resetValues()
    }

    else {
      triggerModal(TRIGGER_CREATE_MODAL)
    }
  }

  handleDelete = () => {
    const { triggerModal } = this.props

    triggerModal(TRIGGER_DELETE_MODAL)
  }

  render () {
    const { title, edit, intl: { formatMessage }, breakpoint } = this.props
    const nameSetterPlaceholder = formatMessage({id: 'meeting.eventNameSetter.placeholder'})
    const nameSetterLabel = formatMessage({id: 'meeting.eventNameSetter.label'})
    const dropdownPlaceholder = formatMessage({id: 'meeting.dropdown.placeholder'})
    const dropdownLabel = formatMessage({id: 'meeting.dropdown.label'})
    const deleteBtnText = formatMessage({id: 'meeting.edit.deleteBtnText'})

    return (
      <FocusTrap>
        <Section css={this.containerStyles}>
          <Div css={this.contentStyles}>
            <Div css={this.childrenStyles}>
              <LargeHeadline css={this.headlineStyles} tabIndex="0">{title}</LargeHeadline>
            </Div>

            <Div css={this.childrenStyles}>
              <EventNameSetter
                label={nameSetterLabel}
                placeholder={nameSetterPlaceholder}
                css={this.innerSectionStyles}
              />

              <DateAndTime css={this.innerSectionStyles} />

              <CloseBtn css={this.closeBtnStyles} onClick={this.closeModal} />
            </Div>

            <Div css={this.childrenStyles}>
              <Dropdown
                label={dropdownLabel}
                placeholder={dropdownPlaceholder}
                css={this.innerSectionStyles}
              />

              <RoomCards
                css={this.innerSectionStyles}
              />
            </Div>

            {window.innerWidth <= breakpoint && edit &&
              <DeleteBtn 
                text={deleteBtnText}
                onClick={this.handleDelete}
                css={this.deleteBtnStyles}
              />
            }
          </Div>

          <BottomBtns
            onCancel={this.closeModal}
            onDelete={this.handleDelete}
            edit={edit}
          />
        </Section>
      </FocusTrap>
    )
  }
}

const mapStateToProps = state => ({
  edit: state.pageReducer.editEventModalOpen,
  breakpoint: state.pageReducer.mobileBreakpoint
})

const mapDispatchToProps = {
  triggerModal,
  resetValues
}

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(Meeting))


