import React, { Component } from 'react'
import { Div, Img, Span } from 'glamorous'
import Paragarph from './Paragraph'
import GreyParagraph from './GreyParagraph'
import PropTypes from 'prop-types'

export default class DropdownOption extends Component {
  static propTypes = {
    name: PropTypes.string.isRequired,
    avatarSrc: PropTypes.string.isRequired,
    homeFloor: PropTypes.number.isRequired,
    highlighted: PropTypes.bool.isRequired,
    onClick: PropTypes.func.isRequired,
    id: PropTypes.string
  }

  containerStyles = {
    height: '35px',
    cursor: 'pointer',
    display: 'flex',
    backgroundColor: '#ffffff',
    alignItems: 'center',
    padding: '0px 15px',
    transition: 'all .15s ease-in-out',
    ':hover': {
      backgroundColor: '#f6f7f9',
    }
  }

  avatarStyles = {
    borderRadius: '100%',
    width: '25px',
    height: '25px',
    marginRight: '10px'
  }

  paragraphStyles = {
    whiteSpace: 'nowrap',
    overflow: 'hidden',
    textOverflow: 'ellipsis'
  }

  greyParagraphStyles = {
    fontWeight: 'normal',
    fontSize: '16px',
    textTransform: 'lowercase',
    whiteSpace: 'nowrap',
  }

  highlightedStyles = {
    fontWeight: 'bold'
  } 

  render() {
    const { name, avatarSrc, homeFloor, highlighted, id, onClick } = this.props
    let paragraphStyles = this.paragraphStyles
    let greyParagraphStyles = this.greyParagraphStyles
    let containerStyles = this.containerStyles

    if(highlighted) {
      paragraphStyles = { ...this.paragraphStyles, ...this.highlightedStyles }
      greyParagraphStyles = { ...this.greyParagraphStyle, ...this.highlightedStyles }
      containerStyles = { ...this.containerStyles, backgroundColor: '#f6f7f9' }
    }

    return (
      <Div css={containerStyles} id={id} onClick={onClick}>
        <Img css={this.avatarStyles} src={avatarSrc} alt={`${name}`} />
        <Paragarph css={paragraphStyles}>{name} ¬∑&#160;</Paragarph>
        <GreyParagraph css={greyParagraphStyles}>
          <Span css={{fontSize: '17px'}}>{homeFloor}</Span> —ç—Ç–∞–∂
        </GreyParagraph>
      </Div>
    );
  }
}


