import React from 'react'
import FontAwesomeIcon  from 'react-fontawesome'
import RoundBtn from './RoundBtn'
import { injectIntl } from 'react-intl'

export default injectIntl(props => {
  const { intl: { formatMessage }, ...otherProps } = props
  const label = formatMessage({id: 'closeBtnAriaLabel'})

  return (
    <RoundBtn {...otherProps} aria-label={label}>
      <FontAwesomeIcon 
        name="times"
      />
    </RoundBtn>
  )
})


