import updateLocalStorageEvent from '../../db/queries/updateLocalStorageEvent'
import { EDIT_EVENT } from '../../consts/eventsReducer'

const editEventActionCreator = (id, updatedEvent) => ({
  type: EDIT_EVENT,
  id,
  updatedEvent
})

const editEvent = (updatedEvent, id) => {
  return dispatch => {
    updateLocalStorageEvent(updatedEvent, id)
    dispatch(editEventActionCreator(id, updatedEvent))
  }
}

export default editEvent


