import React, { Component } from 'react'
import { Div, Img, Button } from 'glamorous'
import Paragraph from './Paragraph'
import Cross from 'react-fontawesome'
import PropTypes from 'prop-types'
import { injectIntl, intlShape } from 'react-intl'

class ParticipatorCard extends Component {
  static propTypes = {
    intl: intlShape.isRequired,
    avatarUrl: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    removeUser: PropTypes.func.isRequired,
  }

  containerStyles = {
    width: 'auto',
    backgroundColor: '#e9ecef',
    height: '25px',
    display: 'inline-flex',
    borderRadius: '25px',
    position: 'relative',
    alignItems: 'flex-start',
    marginTop: '10px',
    marginLeft: '10px',
    paddingRight: '35px',
    border: 'none',
    cursor: 'pointer',
    outline: 'none',
    transition: '.15s ease-in-out',
    ':focus,:hover': {
      backgroundColor: '#dfe4e8'
    },
    '@media(max-width: 540px)': {
      width: '100%',
      height: '30px',
      marginLeft: '5px',
      paddingRight: '0px'
    }
  }

  imageStyles = {
    width: '25px',
    height: '25px',
    borderRadius: '100%',
    marginLeft: '-10px',
    marginTop: '-1px',
    '@media(max-width: 540px)': {
      width: '30px',
      height: '30px'
    }
  }

  iconStyles = {
    color: '#b3b8bc',
    position: 'absolute',
    right: '10px',
    marginTop: '3px',
    '@media(max-width: 540px)': {
      marginTop: '6px'
    }
  }

  paragraphStyles = {
    marginLeft: '10px',
    marginTop: '3px',
    '@media(max-width: 540px)': {
      fontSize: '17px'
    }
  }

  render () {
    const { 
      avatarUrl,
      name,
      removeUser,
      intl: {
        formatMessage
      }
    } = this.props
    const label = formatMessage({id: 'participatorCard.label'}, {name})
    
    return (
      <Button 
        css={this.containerStyles} 
        onClick={removeUser} 
        aria-label={label}
      >
        <Img src={avatarUrl} css={this.imageStyles} />
        <Paragraph css={this.paragraphStyles}>{name}</Paragraph>
        <Div css={this.iconStyles}>
          <Cross
            name="times"
          />
        </Div>
      </Button>
    )
  }
}

export default injectIntl(ParticipatorCard)


